"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import Link from "next/link";

const AnimeDetail = () => {
  const router = useRouter();
  const { id } = router.query;

  const [anime, setAnime] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  useEffect(() => {
    async function loadAnime() {
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}`);
        const json = await res.json();
        setAnime(json.data);
      } catch {
        setError(true);
      } finally {
        setLoading(false);
      }
    }

    if (id) {
      loadAnime();
    }
  }, [id]);

  if (loading) return <p style={{ color: "white", textAlign: "center" }}>Carregando...</p>;
  if (error || !anime) return <p style={{ color: "white", textAlign: "center" }}>Erro ao carregar anime.</p>;

  return (
    <div
      style={{
        width: "100%",
        minHeight: "100vh",
        backgroundImage: "url('/detail.jpg')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundAttachment: "fixed",
        padding: 0,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        margin: 0,
      }}
    >
      <button
        onClick={() => {
            // SALVA a flag de recarregamento ANTES de navegar
            sessionStorage.setItem('shouldReloadSearch', 'true');
            
            // Navega para search
            router.push('/search');
            
            // For√ßa recarga se n√£o carregar em 500ms
            setTimeout(() => {
            if (window.location.pathname === '/search') {
                const shouldReload = sessionStorage.getItem('shouldReloadSearch');
                if (shouldReload === 'true') {
                sessionStorage.removeItem('shouldReloadSearch');
                window.location.reload();
                }
            }
            }, 500);
        }}
        style={{
            position: "fixed",
            top: "20px",
            left: "20px",
            zIndex: 1000,
            padding: "10px 20px",
            background: "rgba(0, 0, 0, 0.8)",
            color: "white",
            border: "none",
            borderRadius: "8px",
            cursor: "pointer",
            fontWeight: "bold",
            fontSize: "16px",
            display: "flex",
            alignItems: "center",
            gap: "8px",
            transition: "all 0.3s",
            border: "2px solid #1e88e5"
        }}
        onMouseEnter={(e) => {
            e.currentTarget.style.background = "rgba(30, 136, 229, 0.9)";
            e.currentTarget.style.transform = "scale(1.05)";
        }}
        onMouseLeave={(e) => {
            e.currentTarget.style.background = "rgba(0, 0, 0, 0.8)";
            e.currentTarget.style.transform = "scale(1)";
        }}
        >
        <span style={{ fontSize: "20px" }}>‚Üê</span>
        <span>Voltar para Pesquisa</span>
        </button>

      <h1 style={{ color: "white", marginBottom: 20, marginTop: "80px" }}>{anime.title}</h1>

      <div
        style={{
          display: "flex",
          gap: 20,
          maxWidth: "1000px",
          flexDirection: "column",
          alignItems: "center",
          padding: "20px",
          background: "rgba(0, 0, 0, 0.7)",
          borderRadius: "8px",
          backdropFilter: "blur(5px)",
        }}
      >
        <div style={{ display: "flex", gap: 20, alignItems: "flex-start" }}>
          <img
            src={anime.images?.jpg?.image_url}
            alt={anime.title}
            width={200}
            height={300}
            style={{
              objectFit: "cover",
              borderRadius: "8px",
              boxShadow: "0 4px 8px rgba(0, 0, 0, 0.6)",
            }}
          />

          <div
            style={{
              color: "white",
              textAlign: "left",
              maxWidth: "800px",
            }}
          >
            <p><strong>Ano:</strong> {anime.year || "Desconhecido"}</p>
            <p><strong>Score:</strong> {anime.score || "N/A"}</p>
            <p><strong>Status:</strong> {anime.status || "N/A"}</p>
            <p><strong>G√™nero:</strong> {anime.genres?.map((genre: any) => genre.name).join(", ") || "Desconhecido"}</p>
            <p><strong>Tipo:</strong> {anime.type || "Desconhecido"}</p>
            <p><strong>Quant. de epis√≥dios:</strong> {anime.episodes || "N/A"}</p>
          </div>
        </div>

        <div
          style={{
            maxHeight: "200px",
            overflowY: "auto",
            marginTop: "20px",
            whiteSpace: "pre-wrap",
            color: "white",
            width: "100%",
          }}
        >
          <p><strong>Sinopse:</strong> {anime.synopsis || "Sinopse n√£o dispon√≠vel"}</p>
        </div>

        <div style={{ 
          display: "flex", 
          gap: 15, 
          marginTop: 30,
          justifyContent: "center",
          width: "100%"
        }}>
          <Link href={`/${id}/characters`} passHref>
            <button
              style={{
                padding: "12px 25px",
                backgroundColor: "#1e88e5",
                color: "white",
                border: "none",
                borderRadius: "5px",
                cursor: "pointer",
                fontWeight: "bold",
                fontSize: "16px",
                transition: "transform 0.2s",
              }}
              onMouseEnter={(e) => e.currentTarget.style.transform = "scale(1.05)"}
              onMouseLeave={(e) => e.currentTarget.style.transform = "scale(1)"}
            >
              üë• Ver Personagens
            </button>
          </Link>
          
          <Link href={`/${id}/staff`} passHref>
            <button
              style={{
                padding: "12px 25px",
                backgroundColor: "#43a047",
                color: "white",
                border: "none",
                borderRadius: "5px",
                cursor: "pointer",
                fontWeight: "bold",
                fontSize: "16px",
                transition: "transform 0.2s",
              }}
              onMouseEnter={(e) => e.currentTarget.style.transform = "scale(1.05)"}
              onMouseLeave={(e) => e.currentTarget.style.transform = "scale(1)"}
            >
              üé¨ Ver Staff
            </button>
          </Link>
          
          <Link href={`/${id}/videos`} passHref>
            <button
              style={{
                padding: "12px 25px",
                backgroundColor: "#f4511e",
                color: "white",
                border: "none",
                borderRadius: "5px",
                cursor: "pointer",
                fontWeight: "bold",
                fontSize: "16px",
                transition: "transform 0.2s",
              }}
              onMouseEnter={(e) => e.currentTarget.style.transform = "scale(1.05)"}
              onMouseLeave={(e) => e.currentTarget.style.transform = "scale(1)"}
            >
              üì∫ Ver V√≠deos
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default AnimeDetail;